@tailwind base;
@tailwind components;
@tailwind utilities;

.boxes {
  @apply bg-home-cards flex flex-col justify-center items-center w-72 h-80 p-6 gap-5 rounded-md drop-shadow-xl text-white mb-2;
}

.icons {
  @apply rounded-full w-20 h-20 bg-white flex justify-center items-center text-black;
}

.button {
  @apply tracking-wider bg-white border text-black border-black px-4 w-52 py-2 mb-5  font-semibold text-xl hover:bg-black hover:border-white  hover:text-white drop-shadow-lg;
}

.quantityButton {
  @apply bg-gray-300 px-2 w-8 h-6  font-semibold flex justify-center items-center text-center hover:enabled:bg-black hover:enabled:text-white transition-all duration-300;
}

.bg-black1 {
  @apply bg-gray-300 rounded-md bg-clip-padding backdrop-filter-none backdrop-blur-sm bg-opacity-90;
}

.checkout-btn {
  @apply bg-black border text-white border-black px-4 w-52 py-2 mb-5  font-semibold text-xl hover:bg-green-500 hover:border-white  hover:text-white drop-shadow-lg;
}
